import React from 'react';
import './ServiciosInicio.css';
import ConsultoriaFarmaceutica_img from '../../../img/servicios/ConsultoriaFarmaceutica.png';
import CalificacionEquipos_img from '../../../img/servicios/CalificacionEquipos.png';
import CalificacionSalas_img from '../../../img/servicios/CalificacionSalas.png';
import MapeosTermicos_img from '../../../img/servicios/MapeosTrmicos.png';

const ServiciosInicio = () => {
  const servicios = [
    {
      nombre: 'Consultara farmacutica',
      descripcion: 'Proporcionamos asesoramiento experto para optimizar procesos, garantizar el cumplimiento regulatorio y mejorar la eficiencia en la gestin de productos y equipos farmacuticos.',
      imagen: ConsultoriaFarmaceutica_img
    },
    {
      nombre: 'Calificacin de Salas',
      descripcion: 'Proporcionamos evaluacin y certificacin de clnicas y quirfanos para asegurar el cumplimiento de estndares de calidad, seguridad y normativa sanitaria.',
      imagen: CalificacionEquipos_img
    },
    {
      nombre: 'Calificacin de equipos',
      descripcion: 'Ofrecemos evaluacin y certificacin de equipos clnicos e industriales para garantizar su desempeo, seguridad y cumplimiento de normativas.',
      imagen: CalificacionSalas_img
    },
    {
      nombre: 'Mapeos trmicos',
      descripcion: 'Ofrecemos anlisis detallado de la distribucin de temperatura en instalaciones para optimizar el acondicionamiento y asegurar el cumplimiento de estndares de calidad y eficiencia energtica.',
      imagen: MapeosTermicos_img
    },
    // Puedes aadir ms servicios aqu
  ];

  return (
    <div className="servicios-contenedor">
      <h2>SERVICIOS QUE OFRECEMOS</h2>
      <div className="servicios">
        {servicios.map((servicio, index) => (
          <div key={index} className="servicio">
            <div className="servicio-imagen"> {/* Imagen despus */}
              <img src={servicio.imagen} alt={servicio.nombre} />
            </div>
            <div className="servicio-info"> {/* Info primero */}
              <h3>{servicio.nombre}</h3>
              <div className="contenedor-descripcion">
                <p>{servicio.descripcion}</p>
              </div>
              <button className="leer-mas">Leer ms</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ServiciosInicio;
