import React, { useState } from 'react';
import './GestionCadena.css'
import Modal from '../PopUp/Modal';
import ReactMarkdown from 'react-markdown';
import Imagen from '../../Assets/Servicios/consultoria.png'

// Contenido de tu archivo Markdown
const markdownContent = `# Gestin de cadena de suministros

En nuestra empresa, nos especializamos en la gestin eficiente de la cadena de suministros para distribuidores de insumos y partes de equipos mdicos, ofreciendo un espectro completo de servicios diseados para optimizar las operaciones, reducir costos y mejorar la satisfaccin del cliente. Nuestros servicios incluyen:

- **Adquisicin Estratgica:** Identificamos y nos asociamos con proveedores de primera lnea para garantizar la calidad y la disponibilidad constante de insumos y partes crticas. Nuestro enfoque en la negociacin y la gestin de relaciones con proveedores nos permite obtener las mejores condiciones, incluyendo precios competitivos y plazos de entrega ptimos.
- **Gestin de Inventarios:** Implementamos soluciones avanzadas de gestin de inventarios, incluyendo sistemas de seguimiento y pronstico, para asegurar que los productos esenciales estn siempre disponibles sin incurrir en excesos que generen costos de almacenamiento innecesarios.
- **Logstica y Distribucin:** Nuestra red logstica garantiza una distribucin eficiente y oportuna de insumos y partes a cualquier punto del pas. Nos enfocamos en la optimizacin de rutas y la seleccin de transportistas para reducir los tiempos de entrega y los costos asociados.
- **Control de Calidad:** Mantenemos los ms altos estndares de calidad en todos los eslabones de la cadena de suministro. Nuestros procesos de inspeccin y validacin aseguran que todos los productos cumplan con las normativas y expectativas de calidad antes de su distribucin.
- **Gestin de Riesgos:** Identificamos y mitigamos proactivamente los riesgos asociados con la cadena de suministros, desde la volatilidad de precios hasta la interrupcin de la cadena de suministro, para garantizar una operacin ininterrumpida y confiable.
- **Cumplimiento Normativo:** Nos aseguramos de que todas las operaciones cumplan con las leyes y regulaciones locales e internacionales, incluyendo las normas de seguridad y ambientales, garantizando una operacin tica y responsable. 
- **Consultora y Mejora Continua:** Ofrecemos servicios de consultora para identificar oportunidades de mejora en la cadena de suministros. Aplicamos metodologas de mejora continua para optimizar los procesos, aumentar la eficiencia y reducir costos.

Al confiar en nosotros la gestin de parte de su cadena de suministros, los distribuidores de insumos y partes de equipos mdicos pueden enfocarse en su core business, con la seguridad de que su operacin logstica est en manos de expertos. Estamos comprometidos con la excelencia operativa y la innovacin, con el objetivo de superar las expectativas de nuestros clientes y contribuir al xito de su negocio.
`;

function GestionCadena() {
    const [isModalOpen, setIsModalOpen] = useState(false);

  return (
    <div className="contenedor-gestion-cadena">
        <div className="gestion-cadena-left">
            <img src={Imagen} alt="Descripcin" />
        </div>
        <Modal isOpen={isModalOpen} close={() => setIsModalOpen(false)}>
                <ReactMarkdown>{markdownContent}</ReactMarkdown>
        </Modal>
        <div className="gestion-cadena-right">
            <h2>Gestin de cadena de suministros</h2>
            <button className="btn-ver-servicios" onClick={() => setIsModalOpen(true)}>Ver detalles</button>
        </div>
    </div>
    
  )
}

export default GestionCadena

