import React, { useState } from 'react';
import Modal from '../PopUp/Modal';
import ReactMarkdown from 'react-markdown';
import './ControlCalidad.css'
import Imagen from '../../Assets/Servicios/cadena logistica.png';

// Contenido de tu archivo Markdown
const markdownContent = `# Control de calidad 

En Mesafarq, comprendemos la importancia crtica de mantener estndares de calidad excepcionales en la industria de insumos mdicos. Nuestra especialidad radica en la implementacin de prcticas de control de calidad robustas y eficientes que aseguran la integridad de los productos mdicos desde la adquisicin hasta la entrega al cliente.

## Nuestros Servicios

### Auditora y Certificacin de Proveedores
- Evaluamos y auditamos a los proveedores para asegurarnos de que cumplen con las regulaciones nacionales e internacionales.
- Implementamos programas de certificacin para garantizar la produccin y suministro continuo de insumos de alta calidad.

### Control de Calidad en el Proceso de Manufactura
- Supervisamos las operaciones de fabricacin para garantizar la conformidad con las especificaciones del producto.
- Realizamos pruebas en el sitio y anlisis de laboratorio para validar la calidad y seguridad del producto.

### Gestin de Documentacin Regulatoria
- Mantenemos y gestionamos toda la documentacin requerida para cumplir con las regulaciones vigentes.
- Asistimos en la preparacin y presentacin de documentos regulatorios para nuevos productos o cambios en productos existentes.

### Inspeccin y Pruebas de Producto Final
- Realizamos inspecciones detalladas y pruebas de calidad de los productos finales antes del envo.
- Nos aseguramos de que cada lote cumpla con las especificaciones de calidad y las expectativas del cliente.

### Gestin de No Conformidades y Acciones Correctivas
- Identificamos productos no conformes y implementamos acciones correctivas rpidamente.
- Analizamos las causas de la no conformidad para mejorar los procesos y prevenir futuras incidencias.

### Capacitacin y Desarrollo del Personal
- Ofrecemos programas de capacitacin para el personal sobre prcticas de calidad y conformidad regulatoria.
- Fomentamos una cultura de calidad y mejora continua en toda la organizacin.

### Monitoreo Continuo y Mejora de la Calidad
- Utilizamos mtricas de calidad y herramientas de monitoreo para evaluar continuamente los procesos de calidad.
- Implementamos iniciativas de mejora de la calidad basadas en datos para optimizar los procedimientos operativos.

## Compromiso con la Excelencia
En Mesafarq, nuestro compromiso es proporcionar servicios de control de calidad que no solo cumplen, sino que superan las normativas y expectativas de la industria mdica. Nuestra meta es ser su aliado estratgico en la mejora y mantenimiento de la calidad superior de sus productos.

Confe en nosotros para elevar los estndares de sus insumos mdicos y llevar su empresa al siguiente nivel de excelencia operativa.
`;

function ControlCalidad() {
    const [isModalOpen, setIsModalOpen] = useState(false);

    return (
      <div className="contenedor-control-calidad">
          <div className="control-calidad-left">
              <h2>Control de calidad de negocios en salud</h2>
              <button className="btn-ver-servicios" onClick={() => setIsModalOpen(true)}>Ver detalles</button>
          </div>
          <Modal isOpen={isModalOpen} close={() => setIsModalOpen(false)}>
              <ReactMarkdown>{markdownContent}</ReactMarkdown>
          </Modal>
          <div className="control-calidad-right">
              <img src={Imagen} alt="Descripcin" />
          </div>
      </div>
    );
}

export default ControlCalidad;
